import React, { useState, useRef, useEffect } from 'react';
import { Button, Row, Col, Space, Tooltip, message } from 'antd';
import { ReloadOutlined, SearchOutlined, LeftOutlined } from '@ant-design/icons';
import { Link } from 'gatsby';

import CreateColorPrice from './create_color_price';
import ColorPriceQueryDrawerForm from './color_price_query_form';
import ColorPriceList from './color_price_list';
import ColorPriceQueryParams from './color_price_query_params_display';
// ColorPrice 入口组件

const ColorPriceComponent = ({ location }) => {
  // props.location接收传递的参数
  const goodsId = useRef(0);
  const reqParams = useRef({ page: 0 });
  const [goodsTitle, setGoodsTitle] = useState('');

  const [reloadListCount, setReloadListCount] = useState(0);

  const [drawerVisible, setDrawerVisible] = useState(false);

  const [createColorPriceVisible, setCreateColorPriceVisible] = useState(false);

  const queryHandle = (currentRequestParams) => {
    // 查询操作
    reqParams.current = { ...currentRequestParams, page: 0, goods: goodsId.current };
    // 触发重新读取数据
    setReloadListCount(reloadListCount + 1);
    console.log('queryHandle search 1 ...');
  };

  const initReloadList = () => {
    // 刷新列表, 删除所有查询条件
    if (goodsId.current && goodsId.current > 0) {
      reqParams.current = { page: 0, goods: goodsId.current };
      setReloadListCount(reloadListCount + 1);
      console.log('init search 00 ...');
    }else {
      console.log('No goods...');
      message.error('Error: 请返回, 重新选择产品...');
    }
  }

  useEffect(() => {
    // 接收上一页面传递的参数 商品id 重新查询列表
    // console.log(location);
    try {
      console.log(location.state.goodsobj);
      goodsId.current = location.state.goodsobj.id;
      setGoodsTitle(location.state.goodsobj.name);
    } catch (error) {
      console.log(error);
      message.error('Error: 请返回, 重新选择产品...');
    }
    initReloadList();
  }, [location.state])

  return (
    <div>
      <Row>
        <Col span={14}>
          <div style={{width: '100%', height: 'auto', padding: '0.1rem 0.5rem'}}>
            <Space size="large">
              <Tooltip title="返回">
                <Link to="/wxgoods"><LeftOutlined /></Link>
              </Tooltip>
              <h4>ColorPrice</h4>
              <Tooltip title="刷新">
                <Button onClick={initReloadList} shape="circle" icon={<ReloadOutlined />}></Button>
              </Tooltip>
              <Tooltip title="搜索">
                <Button onClick={() => setDrawerVisible(true)} shape="circle" icon={<SearchOutlined />}></Button>
              </Tooltip>
            </Space>
          </div>
        </Col>
        <Col span={10}>
          <div style={{ width: '100%', height: 'auto' }}>
            <Button style={{ float: 'right' }} type="link" onClick={() => setCreateColorPriceVisible(true)}>新建</Button>
          </div>
        </Col>
      </Row>
      <Row>
        <h6>商品标题: {goodsTitle}</h6>
      </Row>
      <Row>
        <Col span={24}><ColorPriceQueryParams paramsObj={reqParams.current}></ColorPriceQueryParams></Col>
      </Row>
      <ColorPriceQueryDrawerForm
        visible={drawerVisible}
        setVisible={setDrawerVisible}
        queryHandle={queryHandle}
      ></ColorPriceQueryDrawerForm>

      <ColorPriceList
        reloadListCount={reloadListCount}
        setReloadListCount={setReloadListCount}
        requestParams={reqParams.current}
      ></ColorPriceList>

      <CreateColorPrice
        visible={createColorPriceVisible}
        setVisible={setCreateColorPriceVisible}
        reloadListCount={reloadListCount}
        setReloadListCount={setReloadListCount}
        goodsId={goodsId.current}
      ></CreateColorPrice>
    </div>
  )
};

export default ColorPriceComponent;
