import React from 'react';

import AxiosRequest from '../../apis';
import { sendAxiosRequest } from '../../apis/sendRequest';

// ColorPrice 的request请求

// ~~~~~~ 需要修改apiLists ~~~~~~
const apiLists = ['goods-admin/v1/goodscolorprice', 'goods-admin/v1/postgoodsstock'];

const goodsStockUpdatePost = (goodsId, colorPriceId, saleNum) => {
    // 更新商品库存
    const requestObj = {goods_id: goodsId, color_price_id: colorPriceId, sale_num: saleNum};

    return new Promise((resolve, reject) => {
        sendAxiosRequest(apiLists[1], 'post', {}, requestObj).then((respObj) => {
            resolve(respObj);
        }).catch((err) => {
            reject(err);
        })
    })
}

const ColorPriceRequestGet = ({ requestCount, reqParams, responseDataHandle }) => {
  // ColorPrice Get请求
  const api = apiLists[0];
  const method = 'get';
  const reqObject = {};

  return (
    <AxiosRequest
      responseDataHandle={responseDataHandle} api={api} method={method}
      reqParams={reqParams} reqObject={reqObject} requestCount={requestCount}
    ></AxiosRequest>
  );
};
ColorPriceRequestGet.defaultProps = {
  requestCount: 0,
  reqParams: {},
  responseDataHandle: (respObj) => { console.log(respObj); },
};

const ColorPriceRequestPost = ({ requestCount, reqObject, responseDataHandle }) => {
  // ColorPrice post请求
  const api = [].concat(apiLists)[0];
  const method = 'post';
  const reqParams = {};
  // const reqObject = {};

  return (
    <AxiosRequest
      responseDataHandle={responseDataHandle} api={api} method={method}
      reqParams={reqParams} reqObject={reqObject} requestCount={requestCount}
    ></AxiosRequest>
  );
};
ColorPriceRequestPost.defaultProps = {
  requestCount: 0,
  reqObject: {},
  responseDataHandle: (respObj) => { console.log(respObj); },
}

const ColorPriceRequestPut = ({ requestCount, reqObject, responseDataHandle, id }) => {
  // ColorPrice put请求
  const api = [].concat(apiLists)[0] + `/${id}`;
  const method = 'put';
  const reqParams = {};
  // const reqObject = {};

  return (
    <AxiosRequest
      responseDataHandle={responseDataHandle} api={api} method={method}
      reqParams={reqParams} reqObject={reqObject} requestCount={requestCount}
    ></AxiosRequest>
  );
};
ColorPriceRequestPut.defaultProps = {
  requestCount: 0,
  reqObject: {},
  responseDataHandle: (respObj) => { console.log(respObj); },
  id: 0
}

const ColorPriceRequestDelete = ({ requestCount, responseDataHandle, id }) => {
  // ColorPrice delete请求
  const api = [].concat(apiLists)[0] + `/${id}`;
  const method = 'delete';
  const reqParams = {};
  const reqObject = {};

  return (
    <AxiosRequest
      responseDataHandle={responseDataHandle} api={api} method={method}
      reqParams={reqParams} reqObject={reqObject} requestCount={requestCount}
    ></AxiosRequest>
  );
};
ColorPriceRequestDelete.defaultProps = {
  requestCount: 0,
  responseDataHandle: (respObj) => { console.log(respObj); },
  id: 0
}

export {
  goodsStockUpdatePost,
  ColorPriceRequestGet,
  ColorPriceRequestPost,
  ColorPriceRequestPut,
  ColorPriceRequestDelete
};
