import React, {
  useState,
  useEffect,
  useRef
} from 'react';

import ProductsList from '../../base_components/products_list';
import ListsItem from '../../base_components/list_item';
import {
  ColorPriceRequestGet
} from './color_price_requests';
// import CreateColorPrice from './create_color_price';
import UpdateColorPrice from './update_color_price';
import {
  updateGoodsRetailPrice
} from '../goods/goods_price_update_handle'

const judgeHasMore = resp => {
    if (Object.keys(resp).includes('next') &&
        typeof resp.next === 'string' &&
        resp.next.length > 5) {
        return true;
    }
    return false;
};

// 列表编辑 页面

const ColorPriceList = ({
    reloadListCount,
    setReloadListCount,
    requestParams
  }) => {
    const [hasMoreState, setHasMoreState] = useState(true);
    const [loading, setLoading] = useState(false);
    const [requesting, setRequesting] = useState(false);
    const reqParams = useRef({
        page: 1
    });

    const [colorPriceData, setColorPriceData] = useState([]);
    const [colorPriceRequestGetCount, setColorPriceRequestGetCount] = useState(0);

    // const [createColorPriceVisible, setCreateColorPriceVisible] = useState(false);
    const [updateColorPriceVisible, setUpdateColorPriceVisible] = useState(false);
    const [selectedObj, setSelectedObj] = useState({});

    useEffect(() => {
        // 加载、更新或创建数据后 重新读取列表
        setColorPriceData([]);
        setLoading(false);
        setHasMoreState(true);
        reqParams.current = {
          ...requestParams, page: requestParams.page + 1
        };
        setRequesting(true);
    }, [reloadListCount]);

    useEffect(() => {
        // 读取更多 useEffect解决异步
        if (hasMoreState && !loading && requesting) {
            setRequesting(false);
            loadMore();
        }
    }, [hasMoreState, loading, requesting])

    const loadMore = () => {
        // 读取更多列表
        if (requestParams.goods && requestParams.goods > 0 && hasMoreState && !loading) {
            // const current_page = Number.isInteger(reqParams.current.page) ? reqParams.current.page + 1 : 1;
            // reqParams.current = { ...reqParams.current };
            // console.log(reqParams.current);
            setHasMoreState(false);
            setColorPriceRequestGetCount(colorPriceRequestGetCount + 1);
            setLoading(true);
        } else {
            console.log('Error state: hasMore is ', hasMoreState, ', loading is ', loading);
        }
    };

    const computeMaxOrMinPrice = (colorPriceList, isMin = true) => {
        // 计算零售价最高或最低数
        let computeKey = 'retail_price';
        let retPrice = 0;
        for (let index = 0; index < colorPriceList.length; index++) {
            const element = colorPriceList[index];
            const currentPrice = element[computeKey] ? element[computeKey] : 0;
            if (retPrice === 0) {
                retPrice = currentPrice;
            }
            if (isMin) {
                if (retPrice > currentPrice && currentPrice > 0) {
                    retPrice = currentPrice;
                }
            } else {
                if (retPrice < currentPrice) {
                    retPrice = currentPrice;
                }
            }
        }
        return retPrice;
    };

    const computeGoodsTotalNum = (colorPriceList) => {
        // 计算商品库存数量
        let retTotalNum = 0;
        for (let index = 0; index < colorPriceList.length; index++) {
            const element = colorPriceList[index];
            const currentNum = element.stock_num > 0 ? element.stock_num : 0;
            retTotalNum = retTotalNum + currentNum;
        }
        return retTotalNum;
    }

    const colorPriceGetResponse = resp => {
        // console.log('resp process...');
        setLoading(false);
        if (Object.keys(resp).includes('code') &&
            resp.code === 1000 &&
            Object.keys(resp).includes('results')
        ){
            setColorPriceData(colorPriceData.concat(resp.results));
            // setHasMore(judgeHasMore(resp));   // 判断是否还有更多
            setHasMoreState(judgeHasMore(resp));

            // 更新价格, 总库存数量
            const resultsList = resp.results;
            if (resultsList.length > 0) {
                console.log(resultsList);
                const minPrice = computeMaxOrMinPrice(resultsList);
                const maxPrice = computeMaxOrMinPrice(resultsList, false);
                const totalNum = computeGoodsTotalNum(resultsList);
                console.log(minPrice, maxPrice, resultsList[0].goods);
                updateGoodsRetailPrice(minPrice, maxPrice, totalNum, resultsList[0].goods)
                    .then((respObj) => {
                        console.log(respObj);
                    })
                    .catch(err => {
                        console.log(err);
                    });
            }
        } else {
            console.log('Error: get data...');
        }
    };

    const itemDetailClick = (index) => {
        // 打开详情编辑窗口
        // console.log(colorPriceData[index]);
        let selObj = {
            ...colorPriceData[index]
        };
        console.log(selObj);
        // 日期转 moment 时间
        // selObj.end_time = moment(dateFormat(selObj.end_time), 'YYYY-MM-DD HH:mm:ss');
        setSelectedObj(selObj);
        setUpdateColorPriceVisible(true);
    };

    const ItemRendererComponent = ({ item, index }) => {
        // 必须 每一行渲染 的组件, 修改 ~~~~~~~ item keys ~~~~~~;
        console.log(item);
        return (
            <div>
                <ListsItem listsIndex={index}
                    url={item.property_pic_url}
                    productTitle={item.color_name}
                    productEffects={`零售价: ${item.retail_price}; 团购价: ${item.group_price}; 代理价: ${item.agent_price}; 库存数量: ${item.stock_num}; 说明: ${item.explains}`}
                    sourcePrice={item.retail_price}
                    salePrice={item.agent_price}
                    clickDetailHandle={itemDetailClick}
                ></ListsItem>
            </div>);
      };

    return (
        <div>
            <ColorPriceRequestGet requestCount={colorPriceRequestGetCount}
                reqParams={reqParams.current}
                responseDataHandle={colorPriceGetResponse}
            ></ColorPriceRequestGet>
            <ProductsList productsData={colorPriceData}
                loadMore={loadMore}
                hasMore={hasMoreState}
                ItemRendererComponent={ItemRendererComponent}></ProductsList>

            <UpdateColorPrice visible={updateColorPriceVisible}
                setVisible={setUpdateColorPriceVisible}
                oldObj={selectedObj}
                reloadListCount={reloadListCount}
                setReloadListCount={setReloadListCount}
            ></UpdateColorPrice>
        </div>
    );
}

ColorPriceList.defaultProps = {
    reloadListCount: 0,
    setReloadListCount: (addedNum) => {
        console.log(addedNum);
    },
    requestParams: {
        page: 0
    }
}

export default ColorPriceList;
