import { sendAxiosRequest } from '../../apis/sendRequest';

const apiLists = ['goods-admin/v1/goods'];

// 商品价格更新操作

const updateGoodsRetailPrice = (minRetailPrice, retailPrice, totalGoodsNumber, goodsId = 0) => {
  // 更新最低和最高零售价格 和 总库存数量
  const api = apiLists[0] + '/' + goodsId;
  const method = 'put';
  const reqObject = {"goods_number": totalGoodsNumber, "min_retail_price": minRetailPrice, "retail_price": retailPrice};
  return new Promise((resolve, reject) => {
    // console.log(`argument: ${goodsId}, copy: ${handleGoodsId}`);
    if (goodsId > 0) {
      sendAxiosRequest(api, method, {}, reqObject)
      .then(res => {
        // console.log(res);
        resolve(res);
      })
      .catch(err => {
        // console.log(err);
        reject(err);
      })
    } else {
      reject('Argument goodsId must..., ', goodsId);
    }
  });
};

export { updateGoodsRetailPrice };
