import React, { useRef, useState, useEffect } from 'react';
import { Modal, Divider, message } from 'antd';

import { StsUploadImagesList } from '../../upload_files/sts_upload_files_list';
import { ColorPriceRequestPost } from './color_price_requests';
import { CustomForm } from '../../base_components/form_component';

import {
  formKeys,
  formDisabledKeys,
  formNotShowKeys,
  formKeysLabelsObj,
  formKeySelectComponentsObj,
  formKeysIsImages,
  formGetRequestParamsKeys,
  formInitialObj,
  imageKeyName
} from './color_price_columns_label_config';

// 新建 Modal

const CreateColorPrice = ({ visible, setVisible, reloadListCount, setReloadListCount, goodsId }) => {

  const color_priceImageObj = useRef({});
  const oldObj = useRef({createCount: 0});
  const createFormInitialObj = useRef({});

  const [initFormCount, setInitFormCount] = useState(0);

  const [postRequestCount, setPostRequestCount] = useState(0);
  const reqObject = useRef({});

  useEffect(() => {
    // 更新初始化商品ID
    let updatedInitObj = { ...formInitialObj };
    updatedInitObj.goods = goodsId;
    createFormInitialObj.current = updatedInitObj;
    setInitFormCount(initFormCount + 1);  // 初始化表格
  }, [goodsId]);

  const postResponseDataHandle = (respObj) => {
    // 表单提交后的操作
    console.log(respObj);
    if (Object.keys(respObj).includes('code')
      && respObj.code === 1000) {
      closeModal();
      setReloadListCount(reloadListCount + 1)
    } else {
      message.error('网络错误...');
    }
  }

  const closeModal = () => {
    // 初始化表格和图片 并关闭Modal
    color_priceImageObj.current = {};  // 清空图片数据
    oldObj.current = {createCount: oldObj.current.createCount + 1};  // 初始化图片
    setInitFormCount(initFormCount + 1);  // 初始化表格
    setVisible(false);
  }

  const colorPriceImageUploaded = (imagesObj) => {
    // 图片上传后操作
    color_priceImageObj.current = { ...imagesObj };
  }

  const onFinish = (values) => {
    // 提交表单
    let upObj = { ...values, ...color_priceImageObj.current };
    // let newDate = new Date(upObj.end_time);
    // upObj.end_time = parseInt(newDate.getTime());    // 时间转时间戳
    reqObject.current = upObj;
    setPostRequestCount(postRequestCount + 1);
  }

  return (
    <>
       <Modal visible={visible}
        title="ColorPrice 新建"
        closable={false}
        footer={null}
      >
        <div style={{ width: '100%', height: 'auto', textAlign: 'center' }}>
          <StsUploadImagesList
            oldObj={oldObj.current}
            imageKeyName={imageKeyName}
            onChange={colorPriceImageUploaded}></StsUploadImagesList>
        </div>
        <Divider></Divider>

        <CustomForm
          columns={formKeys}
          disabledKeys={formDisabledKeys}
          notShowKeys={formNotShowKeys}
          formKeyLabelObj={formKeysLabelsObj}
          formKeySelectComponentsObj={formKeySelectComponentsObj}
          onFinish={onFinish}
          cancelHandle={closeModal}
          showDeleteButton={false}
          formInitialObj={createFormInitialObj.current}
          initFormCount={initFormCount}
        ></CustomForm>

      <ColorPriceRequestPost
        requestCount={postRequestCount}
        reqObject={reqObject.current}
        responseDataHandle={postResponseDataHandle}></ColorPriceRequestPost>
      </Modal>
    </>
  )
}

CreateColorPrice.defaultProps = {
  visible: false,
  setVisible: (val) => console.log(val),
  reloadListCount: 0,
  setReloadListCount: (booleanVal) => console.log(booleanVal),
  goodsId: 0
}

export default CreateColorPrice;
